import React from 'react';
import Select from 'react-select'
import makeAnimated from 'react-select/animated';
import { Button, Modal, Form, InputGroup } from 'react-bootstrap';
import "../../../../../assets/scss/plugins/forms/react-select/_react-select.scss";
import { Search } from 'react-feather';

const animatedComponents = makeAnimated();

class WorkFlowSetupModal extends React.Component {
    render() {
        return (
            <div>
                <Modal
                    show={this.props.show}
                    onHide={this.props.handleHide}
                >
                    <Modal.Header closeButton>
                        <Modal.Title id="AddUpdate">
                            {this.props.InsertUpdate} WorkFlow Setup
                        </Modal.Title>
                    </Modal.Header>
                    <Form noValidate validated={this.props.validated} onSubmit={this.props.handleSubmit} autoComplete="off">
                        <Modal.Body>

                            <Form.Group controlId="WorkflowMasterName">
                                <Form.Label>Workflow Master</Form.Label>
                                <Form.Control type="text" placeholder="Enter Workflow Master Name"
                                    value={this.props.WorkflowMasterName}
                                    onChange={this.props.changeWorkflowMasterName}
                                    name="WorkflowMasterName"
                                    required
                                    readOnly
                                />
                                <Form.Control.Feedback type="invalid">
                                    Please provide a valid workflow name.
                                </Form.Control.Feedback>
                            </Form.Group>

                            <Form.Group controlId="ApprovalSequence">
                                <Form.Label>Approval Sequence</Form.Label>
                                <Form.Control type="text" placeholder="Enter Approval Sequence"
                                    value={this.props.ApprovalSequence}
                                    onChange={this.props.changeApprovalSequence}
                                    name="ApprovalSequence"
                                    required
                                    readOnly
                                />
                                <Form.Control.Feedback type="invalid">
                                    Please provide a valid approval sequence.
                                </Form.Control.Feedback>
                            </Form.Group>

                            <Form.Group controlId="SystemStatusType">
                                <Form.Label>Group</Form.Label>
                                <InputGroup>
                                    <Form.Control
                                        type="text"
                                        value={this.props.Group}
                                        placeholder="Search Group"
                                        aria-describedby="inputGroupPrepend"
                                        required
                                        onChange={this.props.changeGroup}
                                        readOnly
                                        name="Status"
                                    />
                                    <InputGroup.Prepend>
                                        <Button variant="info" size="sm" onClick={this.props.handleShowGroupSearch}>
                                            <Search />
                                        </Button>
                                    </InputGroup.Prepend>
                                </InputGroup>
                                <Form.Control.Feedback type="invalid">
                                    Please Select Status
                                 </Form.Control.Feedback>
                            </Form.Group>

                            <Form.Group controlId="Approvers">
                                <Form.Label>Approvers</Form.Label>
                                <Select 
                                    isMulti
                                    name="Approvers"
                                    closeMenuOnSelect={false}
                                    components={animatedComponents}
                                    defaultValue={this.props.Approvers}
                                    options={this.props.GroupUsersData} 
                                    onChange={this.props.changeApprovers}
                                />                                
                            </Form.Group>

                        </Modal.Body>

                        <Modal.Footer>
                            <Button variant="success" onClick={this.props.handleHide}>
                                Cancel
                            </Button>
                            <Button variant="primary" type="submit" disabled={this.props.isEnabled}>
                                {this.props.InsertUpdate}
                            </Button>
                        </Modal.Footer>
                    </Form>
                </Modal>
            </div>
        );
    }
}

export default WorkFlowSetupModal;