import React from 'react';
import { connect } from "react-redux";
import { Helmet } from "react-helmet";
import { Card, CardBody, CardHeader, CardTitle, Row, Col } from "reactstrap"
import Breadcrumbs from "../../../../components/@vuexy/breadCrumbs/BreadCrumb"
import * as API from "../../../../utility/crud/app.crud";
import * as Date from "../../../../utility/dateAndTime";
import { toastr } from 'react-redux-toastr'
import WorkFlowSetupTable from "./Tables/WorkFlowSetupTable";
import WorkFlowSetupModal from "./Modals/WorkFlowSetupModal";
import GroupSearch from '../../SearchComponents/GroupSearch/GroupSearch';

class WorkFlowSetup extends React.Component {
    constructor(props, context) {
        super(props, context);
        
        this.state = {
            validated: false,
            columns: [],
            data: [],
            filteredData: [],
            filteredGroupsData: [],
            value: '',
            show: false,
            showdelete: false,

            WORKFLOW_SETUPS_ID: 0,

            WORKFLOW_MASTER_ID: 0,
            WORKFLOW_MASTER_NAME: '',
            APPROVAL_SEQUENCE: 0,
            GROUP_USERS: '',

            GROUP_ID: 0,
            GROUP_NAME: '',

            USER_ID: 0,
            USER_NAME: '',
            EMAIL: '',

            CREATED_BY: props.user.EMAIL,
            MODIFIED_BY: props.user.EMAIL,
            CREATED_DATE: Date.ShowDate(),
            MODIFIED_DATE: Date.ShowDate(),
            InsertUpdate: '',

            opengroupsearch: false,

            MODULEYPEDESCRIPTION: '',
            SYSTEMSTATUSTYPEDESCRIPTION: '',

            searchcolumns: [],

            searchdata: [],
            searchgroupsdata: [],

            usersdata: [],
            groupusersdata: [],
            selectedApprovers: [],

            searchFilteredGroupsData: [],
            searchFilteredUsersData: [],

            searchgroupsvalue: '',
        }

        this.handleShow = () => {
            this.setState({
                show: true,
                InsertUpdate: 'Insert'
            });
            this.clearData();
        }

        this.handleHide = () => {
            this.setState({
                show: false
            });
        }

        this.handleHideDelete = () => {
            this.setState({
                showdelete: false
            });
        }

        this.handleShowGroupSearch = () => {
            this.setState({ opengroupsearch: true });
        };

        this.handleHideGroupSearch = () => {
            this.setState({ opengroupsearch: false });
        };

        this.populateData();
        this.populateGroups();
        this.populateUsers();

    }

    populateData() {
        const getdata = `
          query{
            WorkFlowSetups {
              WORKFLOW_SETUPS_ID
              WORKFLOW_MASTER_ID
              WORKFLOW {
                WORKFLOW_MASTER_ID
                WORKFLOW_MASTER_NAME
                SET_MODULE_ID   
                MODULE_TYPE{
                  MODULE_ID
                  MODULE_NAME
                }
                SYS_STATUS_ID
                SYS_STATUS_TYPE{
                  SYS_STATUS_ID
                  SYS_STATUS_NAME
                }
                APPROVAL_LEVEL
              },
              APPROVAL_SEQUENCE
              APPROVAL_LEVEL
              GROUP_ID
              GROUP {
                GROUP_ID
                GROUP_NAME
              }
              GROUP_USERS
              USERS {
                USER_ID
                USER_NAME
              }
              CREATED_BY
              MODIFIED_BY
              CREATED_DATE
              MODIFIED_DATE
            }
          }
        `;

        API.getData(getdata)
            .then(({ data }) => {
                
                const WorkflowSetups = [];

                data.data.WorkFlowSetups.forEach(wokflowSetup => {
                    if (wokflowSetup.USERS.length > 0){
                        wokflowSetup.USERS_DETAILS =  wokflowSetup.USERS.map(user => user)
                        wokflowSetup.USERS =  wokflowSetup.USERS.map(user => user.USER_NAME)
                    }

                    WorkflowSetups.push(wokflowSetup);
                });
                
                this.setState({
                    data: WorkflowSetups
                })
            }).catch(error => {
                if (error.response || error.request) {
                    toastr.error('Error Loading Data', 'Failed to Load Workflow Master Data');
                }
            })

    }

    populateGroups() {

        const getdata = `
            query {
                Groups {
                    GROUP_ID
                    GROUP_NAME
                }
            }
        `;

        API.getData(getdata)
            .then(({ data }) => this.setState({
                searchgroupsdata: data.data.Groups
            })).catch(error => {
                if (error.response || error.request) {
                    toastr.error('Error Loading Data', 'Failed to Load Modules Data');
                }
            })
    }

    populateUsers() {

        const getdata = `
            query {
                appUsers {
                    USER_ID
                    USER_NAME
                    EMAIL
                    USER_GROUPS {
                        GROUP {
                            GROUP_ID
                            GROUP_NAME
                        }
                    }
                }
            }
        `;

        API.getData(getdata)
            .then(({ data }) => this.setState({
                usersdata: data.data.appUsers
            })).catch(error => {
                if (error.response || error.request) {
                    toastr.error('Error Loading Data', 'Failed to Load Modules Data');
                }
            })
    }

    handleSubmit = (event) => {
        event.preventDefault();
        const validate = event.currentTarget;
        if (validate.checkValidity() === false) {
            event.preventDefault();
            event.stopPropagation();
        }
        else {
            
            if (this.state.InsertUpdate !== 'Insert') {

                let approvers = this.state.selectedApprovers.map(approver => approver.value).toString();
        
                const formEdit = `
                    mutation{
                        updateWorkFlowSetups(
                            WORKFLOW_SETUPS_ID:${this.state.WORKFLOW_SETUPS_ID},
                            WORKFLOW_MASTER_ID:${this.state.WORKFLOW_MASTER_ID},
                            APPROVAL_SEQUENCE:${this.state.APPROVAL_SEQUENCE},
                            GROUP_ID:${this.state.GROUP_ID},
                            GROUP_USERS:"${approvers}",
                            CREATED_BY:"${this.state.CREATED_BY}",
                            MODIFIED_BY:"${this.state.MODIFIED_BY}",
                            CREATED_DATE:"${this.state.CREATED_DATE}",
                            MODIFIED_DATE:"${this.state.MODIFIED_DATE}"
                        ){
                            WORKFLOW_SETUPS_ID
                        }
                    }
                `;

                console.log(formEdit)

                API.update(formEdit)
                    .then(() => {
                        this.clearData();
                        this.handleHide();
                        this.populateData();
                        toastr.info('Update Record', 'Record Updated Successfully');
                    }).catch(error => {
                        console.error(error)
                        if (error.response || error.request) {
                            toastr.error('Error Loading Data', 'Failed to Load Data');
                        }
                    })
            }
        }
        this.setState({
            validated: true
        })
    }

    handleDelete = (event) => {
        event.preventDefault();

        const formDelete = `
            mutation{
                deleteWorkFlowSetups(
                    WORKFLOW_SETUPS_ID:${this.state.WORKFLOW_SETUPS_ID}
                ){
                    WORKFLOW_SETUPS_ID
                }
            }
        `;

        API.remove(formDelete)
            .then(() => {
                this.handleHideDelete();
                this.populateData();
                toastr.warning('Delete Record', 'Record Deleted Successfully');
            }).catch(error => {
                if (error.response || error.request) {
                    toastr.error('Error Loading Data', 'Failed to Load Data');
                }
            })

    }

    handleCloseGroupsSearch = (e) => {

        let GroupUsersData = [];

        this.state.usersdata.forEach(user => {
            if (user.USER_GROUPS.length) {
                user.USER_GROUPS.forEach(userGroup => {
                    if (userGroup.GROUP.GROUP_ID === e.GROUP_ID) {
                        GroupUsersData.push({ value: user.USER_ID, label: user.USER_NAME })
                    }
                })
            }
        });

        this.setState({
            GROUP_ID: e.GROUP_ID,
            GROUP_NAME: e.GROUP_NAME,
            opengroupsearch: false,
            groupusersdata: GroupUsersData,
        });
    };

    handleEditData = (e) => {

        let GroupUsersData = [];
        let selectedApprovers = [];

        this.state.usersdata.forEach(user => {
            if (user.USER_GROUPS.length) {
                user.USER_GROUPS.forEach(userGroup => {
                    if (e.GROUP && e.GROUP.GROUP_ID !== null && userGroup.GROUP.GROUP_ID === e.GROUP.GROUP_ID) {
                        GroupUsersData.push({ value: user.USER_ID, label: user.USER_NAME })
                    }
                })
            }
        });

        if(e.USERS_DETAILS && e.USERS_DETAILS.length) {
            e.USERS_DETAILS.forEach(user => {
                selectedApprovers.push({ value: user.USER_ID, label: user.USER_NAME })
            })
        }

        this.setState({
            WORKFLOW_SETUPS_ID: e.WORKFLOW_SETUPS_ID,
            WORKFLOW_MASTER_ID: e.WORKFLOW.WORKFLOW_MASTER_ID,
            WORKFLOW_MASTER_NAME: e.WORKFLOW.WORKFLOW_MASTER_NAME,
            APPROVAL_SEQUENCE: e.APPROVAL_SEQUENCE,
            GROUP_ID: e.GROUP ? e.GROUP.GROUP_ID : null,
            GROUP_NAME:  e.GROUP ? e.GROUP.GROUP_NAME : null,
            GROUP_USERS: e.GROUP_USERS,
            selectedApprovers: selectedApprovers,
            groupusersdata: GroupUsersData,
            show: true,
            InsertUpdate: 'Update'
        })
    }

    handleDeleteData = (e) => {
        this.setState({
            WORKFLOW_SETUPS_ID: e.WORKFLOW_SETUPS_ID,
            WORKFLOW_MASTER_ID: e.WORKFLOW_MASTER_ID,
            WORKFLOW_MASTER_NAME: e.WORKFLOW_MASTER_NAME,
            APPROVAL_SEQUENCE: e.APPROVAL_SEQUENCE,
            GROUP_ID: e.GROUP_ID,
            GROUP_USERS: e.GROUP_USERS,
            showdelete: true,
            InsertUpdate: 'Delete'
        })
    }

    clearData() {
        this.setState({
            WORKFLOW_SETUPS_ID: '',
            WORKFLOW_MASTER_ID: '',
            WORKFLOW_MASTER_NAME: '',
            APPROVAL_SEQUENCE: '',
            GROUP_ID: '',
            GROUP_USERS: '',
            validated: false
        })
    }

    handleFilter = (e) => {
        let value = e.target.value
        let data = this.state.data
        let filteredData = this.state.filteredData
        this.setState({ value });

        if (value.length) {
            filteredData = data.filter(item => {
                let startsWithCondition =
                    item.WORKFLOW.WORKFLOW_MASTER_NAME.toString().toLowerCase().startsWith(value.toLowerCase()) ||
                    item.APPROVAL_SEQUENCE.toString().toLowerCase().startsWith(value.toLowerCase()) ||
                    item.GROUP_ID.toString().toLowerCase().startsWith(value.toLowerCase()) ||
                    item.GROUP_USERS.toString().toLowerCase().startsWith(value.toLowerCase())

                let includesCondition =
                    item.WORKFLOW.WORKFLOW_MASTER_NAME.toString().toLowerCase().includes(value.toLowerCase()) ||
                    item.APPROVAL_SEQUENCE.toString().toLowerCase().includes(value.toLowerCase()) ||
                    item.GROUP_ID.toString().toLowerCase().includes(value.toLowerCase()) ||
                    item.GROUP_USERS.toString().toLowerCase().includes(value.toLowerCase())
                if (startsWithCondition) {
                    return startsWithCondition
                } else if (!startsWithCondition && includesCondition) {
                    return includesCondition
                } else return null
            })
            this.setState({ filteredData })
        }
    }

    handleSearchGroupFilter = (e) => {
        let value = e.target.value
        let data = this.state.searchgroupsdata
        let filteredGroupsData = this.state.searchFilteredGroupsData
        this.setState({ searchgroupsvalue: value });

        if (value.length) {
            filteredGroupsData = data.filter(item => {
                let startsWithCondition =
                    item.GROUP_NAME.toString().toLowerCase().startsWith(value.toLowerCase())

                let includesCondition =
                    item.GROUP_NAME.toString().toLowerCase().includes(value.toLowerCase())

                if (startsWithCondition) {
                    return startsWithCondition
                } else if (!startsWithCondition && includesCondition) {
                    return includesCondition
                } else return null
            })
            this.setState({ filteredGroupsData })
        }
    }

    render() {

        const { data, columns, value, filteredData, filteredGroupsData, searchgroupsvalue, searchgroupsdata } = this.state

        return (
            <>
                <Helmet><title>Workflow Setup</title></Helmet>

                <React.Fragment>
                    <Breadcrumbs
                        breadCrumbTitle="Admin"
                        breadCrumbParent="Setup"
                        breadCrumbActive="Workflow Setup"
                    />
                    <Row>
                        <Col sm="12">
                            <Card>
                                <CardHeader>
                                    <CardTitle>Workflow Setup</CardTitle>
                                </CardHeader>
                                <CardBody className="rdt_Wrapper">

                                    <WorkFlowSetupTable
                                        role={this.constructor.name}
                                        handleEdit={this.handleEditData}
                                        handleDelete={this.handleDeleteData}
                                        value={value}
                                        data={data}
                                        columns={columns}
                                        filteredData={filteredData}
                                        handleShow={this.handleShow}
                                        handleFilter={this.handleFilter}
                                    />

                                    <WorkFlowSetupModal
                                        validated={this.state.validated}
                                        show={this.state.show}
                                        handleHide={this.handleHide}
                                        InsertUpdate={this.state.InsertUpdate}
                                        handleSubmit={this.handleSubmit}
                                        handleShowGroupSearch={this.handleShowGroupSearch}
                                        handleShowUserSearch={this.handleShowUserSearch}
                                        
                                        WorkflowMasterName={this.state.WORKFLOW_MASTER_NAME}
                                        ApprovalSequence={this.state.APPROVAL_SEQUENCE}

                                        GroupUsersData={this.state.groupusersdata}

                                        Group={this.state.GROUP_NAME}
                                        changeGroup={e => this.setState({ GROUP_NAME: e.target.value })}

                                        Approvers={this.state.selectedApprovers}
                                        changeApprovers={selectedApprovers => this.setState({selectedApprovers}) }

                                        showdelete={this.state.showdelete}
                                        handleHideDelete={this.handleHideDelete}
                                        handleDelete={this.handleDelete}

                                    />

                                    <GroupSearch
                                        handleSearchGroup={this.handleCloseGroupsSearch}
                                        opensearch={this.state.opengroupsearch}
                                        handleHideSearch={this.handleHideGroupSearch}
                                        value={searchgroupsvalue}
                                        data={searchgroupsdata}
                                        handleFilter={this.handleSearchGroupFilter}
                                        filteredData={filteredGroupsData}
                                    />


                                </CardBody>
                            </Card>
                        </Col>
                    </Row>
                </React.Fragment>

            </>

        )
    }
}

const mapStateToProps = ({ auth: { user } }) => ({
    user
});

export default (connect(mapStateToProps)(WorkFlowSetup));